@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), url("/fonts/montserrat/Montserrat-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Montserrat Medium";
  src: local("Montserrat Medium"), url("/fonts/montserrat/Montserrat-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "Montserrat Bold";
  src: local("Montserrat Bold"), url("/fonts/montserrat/Montserrat-Bold.ttf") format("truetype");
}

* {
  box-sizing: border-box;
}

.title {
  position: fixed;
  line-height: 1.25rem;
  display: flex;
  width: 100vw;
  justify-content: space-between;
  padding: 1rem 0.5rem 0.5rem 1rem;

  span:first-child {
    font-family: Montserrat Bold;
    font-size: 1.25rem;
  }
  span:last-child {
    font-size: 0.8rem;
  }
}

.wrapper {
  padding: 0.5rem;
  padding-left: 1rem;
  font-family: Montserrat;

  div > {
    background-color: grey;
  }

  div:last-child {
    margin-right: 1em;
  }
}

.item {
  height: 100%;
  width: 100%;
  pointer-events: none;
}

.label {
  display: block;
  font-family: Montserrat Bold;
  padding-top: 0.4rem;
  padding-bottom: 0.2rem;
}

.price {
  font-family: Montserrat Medium;
  color: gray;
}
